function createMarkup(arr) {
    return arr
        .map(({ preview, original, description }) =>`
    <li class="gallery-item">
   <a class="gallery-link" href="${original}">
   <img class="gallery-image"
   src="${preview}"
   data-source="${original}"
   alt="${description}"/>
   </a>
    </li > 
`)
        .join("");
}

function onClose(e) {
  if (e.code === "Escape") instance.close()
}








